export default {
  name: "ImageDetailPage",
  components: {
    ArrowLeftOutlined
  },
  data() {
    return {
      path: '',
      instance: null
    }
  },
  mounted() {
    console.log('getpath', this.$route.params['path'])
    this.path = 'file://' + this.$route.params['path']
    this.init(this.path)
  },
  methods: {
    init(path) {
      this.instance = new ImageEditor(
          document.querySelector("#tui-image-editor"),
          {
            includeUI: {
              loadImage: {
                path: path,
                name: "",
              },
              initMenu: "draw", // 
              menuBarPosition: "bottom", // 
             theme:{ "common.bi.image": "",
               "common.bisize.width": "0px",
               "common.bisize.height": "0px",
               "loadButton.display": "none",
               "downloadButton.display": "none", },//customTheme,
              locale: locale_zh,
            },
            initMenu: "rotate",
            cssMaxWidth: 1000, // canvas 
            cssMaxHeight: 600, // canvas 


          }

      )
      return customTheme
      //document.getElementsByClassName("tui-image-editor-main")[0].style.top = "45px"; // 
    },
    // 
    save() {
      const base64String = this.instance.toDataURL(); // base64 
      const data = window.atob(base64String.split(",")[1]);
      const ia = new Uint8Array(data.length);
      for (let i = 0; i < data.length; i++) {
        ia[i] = data.charCodeAt(i);
      }
      const blob = new Blob([ia], { type: "image/png" }); // blob 
      const form = new FormData();
      form.append("image", blob);
      // upload file
    },
    saveAs(){

    }
  }
}
